<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';//@1.15.2

  export default {
    name: 'scroll',
    data() {
      return {
        betterScroll: null,
      };
    },
    props: {
      probeType: {
        /*
        ,0123
         0 scroll 
         1 scroll 
         2  scroll 
         3  momentum  scroll 
        */
        type: Number,
        default() {
          return 0;
        },
      },
      pullUpLoad: {
        type: Boolean,
        default() {
          return false;
        },
      },
    },
    methods: {
      scrollTo(x, y, time = 500) {
        this.betterScroll.scrollTo(x, y, time);
      },
      finishPullUp() {
        this.betterScroll.finishPullUp();
      },
      refresh() {
        // better-scroll DOM 
        this.betterScroll.refresh();
        console.log("refresh", this.betterScroll.scrollerHeight);
      },
      y() {
        //y
        return this.betterScroll.y;
      },
    },
    mounted() {
      //   
      this.betterScroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        pullUpLoad: this.pullUpLoad,
        click: true,
      });
      if (this.probeType > 0 && this.probeType < 4) {
        //on()scroll
        this.betterScroll.on('scroll', position => {
          this.$emit("position", position);
        });
      }
      if (this.pullUpLoad) {
        //on()
        this.betterScroll.on("pullingUp", () => {
          this.$emit("pullingUp");
        });
      }
    },
  };
</script>

<style scoped>
  .wrapper {
    overflow: hidden;
  }
</style>
